
div> .bmi-card{
    max-width: 450px;
    padding: 20px;
    background-color: rgba(136, 196, 163, 0.822);
    border-radius: 10px;
}

.card-title{
    color:rgb(42, 38, 105);
    font-size: xx-large;
    font-weight: bold;
    text-shadow: 1px 1px 2px white, 0 0 25px rgb(12, 49, 28), 0 0 5px rgb(39, 87, 57);
}

tr> td:first-child, tr> th:first-child{
    text-align: left ;
    font-weight: bold;
}

tbody> .bg-this{
    color:rgb(42, 38, 105);
    background: rgba(61, 143, 143, 0.486);
}