@import url(https://fonts.googleapis.com/css2?family=Srisakdi:wght@700&display=swap);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  background-image: url("https://storage.needpix.com/rsynced_images/sunrise-438339_1280.jpg");
  background-position-x: right;
  background-size: cover;
  background-color: #9ddb9d; 
  padding-bottom: 10px;
}

.navbar{
  background: rgba(188, 250, 108, 0.603);
}

.navbar-brand{
  font-family: 'Srisakdi', cursive;
  font-style: italic;
  font-weight: bold;
  text-shadow: 2px 2px 5px rgb(50, 112, 74);
}

.nav-link{
  font-weight: bold;
  text-shadow: 2px 2px 5px rgb(96, 163, 122);

}

.fillScr{
  min-height: 1000px; 
}

.fas{
  padding-left: 5px;
}

.col-sm-1 {width: 8.33%;}
.col-sm-2 {width: 16.66%;}
.col-sm-3 {width: 25%;}
.col-sm-4 {width: 33.33%;}
.col-sm-5 {width: 41.66%;}
.col-sm-6 {width: 50%;}
.col-sm-7 {width: 58.33%;}
.col-sm-8 {width: 66.66%;}
.col-sm-9 {width: 75%;}
.col-sm-10 {width: 83.33%;}
.col-sm-11 {width: 91.66%;}
.col-sm-12 {width: 100%;}

@media screen and (min-width: 901px) {
  .col-lg-1 {width: 8.33%;}
  .col-lg-2 {width: 16.66%;}
  .col-lg-3 {width: 25%;}
  .col-lg-4 {width: 33.33%;}
  .col-lg-5 {width: 41.66%;}
  .col-lg-6 {width: 50%;}
  .col-lg-7 {width: 58.33%;}
  .col-lg-8 {width: 66.66%;}
  .col-lg-9 {width: 75%;}
  .col-lg-10 {width: 83.33%;}
  .col-lg-11 {width: 91.66%;}
  .col-lg-12 {width: 100%;}
}


.carousel-contaner{
    border: solid rgb(42, 38, 105);
    border-radius: 5px;
    margin: 5%;
    background-color: rgba(136, 196, 163, 0.822);
}

.item-title{
    min-height: 120px;
    color:rgb(42, 38, 105);
    padding: 2%;
}

img{
    min-height: 400px;
    max-height: 450px;
 }

.btn-con> .btn{
    margin-left: 9%;
    margin-right: 9%;
    width: 110px;
}

.btn-con> .btn{   
    background-color: rgb(80, 136, 136);
}

.title{
    font-family: 'Srisakdi', cursive;
    padding-bottom: 40px;
}

.title> h1{
    font-style: italic;
    font-size: 4em;
    padding: 15px;
    text-shadow: 2px 2px 5px rgb(50, 112, 74);
}

.nutrit{
    padding-bottom: 40px;
    font-style: italic;
    color: rgb(42, 38, 105);
    text-shadow: 2px 2px 5px rgb(50, 112, 74);
}

.nutrit> h2{
    font-size: 1.7em;
}

.carousel-caption{
    text-shadow: 1px 1px 2px black, 0 0 25px rgb(12, 49, 28), 0 0 5px rgb(39, 87, 57);
}

@media screen and (min-width: 901px) {

    .home{
        direction: rtl;     
    }

    .title{
        margin-bottom: 0;
        padding-bottom: 0;
    }

    .title> h1{
        font-size: 5em;
        padding: 20px;
    }

    .nutrit{
        float: right;
        margin-left: 20%;
        margin-right: 10%;
        margin-top: 20px;
    }

    .nutrit> h2{
        font-size:2em;
    }

    .btn-con{
        float: left;
        margin-left: 20%;
        margin-right: 10%;
    }

    .btn-con> .btn{
        display: block;
        margin: 50px;
        width: 220px;
    }
    
    .carousel-contaner{
        min-width: 340px;
        clear: right;
        overflow: auto; 
        margin-right: 10%;
    }

    img{
        min-height: 400px;
        max-height: 410px;
    }

}

form{
    color: rgb(216, 208, 216);
    text-shadow: 1px 1px 2px black, 0 0 25px rgb(12, 49, 28), 0 0 5px rgb(39, 87, 57);
    font-size: x-large;
    font-weight: bold;
    padding-right: 7%;
    padding-left: 7%;   
    margin-bottom: 10px;
}

.form-control{
    border: solid  rgb(50, 112, 74);
    height: 50px;
}

.form-group> .btn{
    margin-top: 5px;
    background-color: rgb(80, 136, 136);
}

.form-title{
    padding: 30px;
    font-style: italic;
    color: rgb(42, 38, 105);
    text-shadow: 2px 2px 5px rgb(50, 112, 74);  
}



.babble{
    color: rgb(42, 38, 105);
}

.name-bdg> h4{
    font-size: 1.7em;
}

.name-bdg{
    margin: 0px
}

.group> .list-item{
    display: table;
    margin-top: 10px;
    background: rgba(136, 196, 163, 0.822);
    color: black;
}

.group{
    margin-top: 30px;
    margin-right: 10%;
    margin-left: 10%;
    min-height: 400px;
}

@media screen and (min-width: 901px) {

    .group> .list-item{
        text-align: left;      
    }

    .babble{
        margin-left: 10%;
    }

    .name-bdg{
        margin-left: 30%;
    }

    
}


div> .bmi-card{
    max-width: 450px;
    padding: 20px;
    background-color: rgba(136, 196, 163, 0.822);
    border-radius: 10px;
}

.card-title{
    color:rgb(42, 38, 105);
    font-size: xx-large;
    font-weight: bold;
    text-shadow: 1px 1px 2px white, 0 0 25px rgb(12, 49, 28), 0 0 5px rgb(39, 87, 57);
}

tr> td:first-child, tr> th:first-child{
    text-align: left ;
    font-weight: bold;
}

tbody> .bg-this{
    color:rgb(42, 38, 105);
    background: rgba(61, 143, 143, 0.486);
}


.trk-pnt-con> button{
    font-size: 1.4em;    
}

@media screen and (min-width: 901px) {

    .gr-con{
        display: block;
        float: left;
        margin-left: 5%;
    }
      
    .weight-graph{
        display: inline-block; 
        max-width: 430px;
    }
    
    .chat-con{
        margin-top: 10%;
        clear: left;
        overflow: auto;  
    }
   
}


.bmi-form{
    margin-top: 20px;          
}

.weight-graph{
    background-color: rgba(136, 196, 163, 0.822);
    border-radius: 5px;
    margin: 5%;
}

@media screen and (min-width: 901px) {

    .g-con{
        float: left;
        margin-top:5%; 
        margin-left: 10%;
    }

      
    .bmi-view{
        float: right;
        margin-top: 5%;
        margin-right: 5%;
    }

    
}

.group> .list-item-ask{
    margin-right: 20%;
    text-align: left;  
    margin-top: 10px;
    background: rgba(136, 196, 163, 0.822);
    color: black;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
    border-top-right-radius: 15px;
}

.group> .list-item-ans{
    margin-left: 20%;
    text-align: left;
    margin-top: 10px;
    background: rgba(204, 238, 219, 0.822);
    color: black;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    border-bottom-right-radius: 15px;
}

.chat-btn{
    color: white;
    height: 50px;
    font-weight: bold;
}

.chat-form{
    margin-top: 20px;
}


.tr-page-title> h1{
    font-size: 1.5em;
}

.selector-con{
    color: rgb(216, 208, 216);
    text-shadow: 1px 1px 2px black, 0 0 25px rgb(12, 49, 28), 0 0 5px rgb(39, 87, 57);
    width: 85%;
    margin:auto;
}

.eat-con, .symp-con{
    margin: 10%;
}

.eat-con> p, .symp-con> p{
    background: rgb(80, 136, 136);
    border-radius: 5px;
}

.t-pick{
    width: 100%;
    border-radius: 5px;
}

.rc-time-picker-clear{
    display: none;
}

.rc-time-picker-input{
    color: black;
    font-size: 15px;
}

select{
    min-width: 100%;
    border-radius: 5px;
}

.eat-con> .btn, .symp-con> .btn{
    background-color: rgb(80, 136, 136);
}

.tr-view-con> .list-group{
    flex-direction: row;
}

.tr-view{  
    display: table;
    font-size: 0.6em;
    margin: 10px;
}

.tr-view> .eat-view, .tr-view> .symp-view {
    display: table-cell;
    text-align: left;
    width: 48%;
    background:  rgba(136, 196, 163, 0.822);
    border-radius: 10px;
}

.mrn> p, .eve> p {
    padding: 1px;   
    margin: 0px;
}

.mrn, .eve{
    padding: 10px;
    margin-bottom: 5px;
    border-radius: 10px;
}

.mrn{
    background: rgba(196, 245, 212, 0.753);   
}
.eve{
    background: rgba(188, 214, 245, 0.767); 
}


@media screen and (min-width: 901px) {

    .tr-page-title> h1{
        font-size: 2em;
    }

    .selector-con{
        float: right; 
    }

    .tr-view{  
        margin-top: 5%;
        float: left;
        margin-left: 15%; 
        font-size: 1em;

    }
}
